export default function jsToCSS(str) {
  const regex = /([A-Z])/gm;
  const matches = str.match(regex);

  let result = str;

  if (matches) {
    for (let i = 0; i < matches.length; i += 1) {
      const replacement = '-' + matches[i].toLowerCase();

      result = result.replace(matches[i], replacement);
    }
  }

  return result;
};
